/*main style file
========================*/
a, abbr, acronym, address, applet, article, aside, audio, b, blockquote, big, body, center, canvas, caption, cite, code, command, datalist, dd, del, details, dfn, dl, div, dt, em, embed, fieldset, figcaption, figure, font, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, html, i, iframe, img, ins, kbd, keygen, label, legend, li, meter, nav, object, ol, output, p, pre, progress, q, s, samp, section, small, span, source, strike, strong, sub, sup, tbody, tfoot, thead, th, tr, tdvideo, tt, u, var {
  margin: 0;
  padding: 0;
  outline: 0;
}

blockquote, q {
  quotes: none;
}

table, table td {
  padding: 0;
}

img {
  vertical-align: top;
  border: 0px;
  max-width: 100%;
  height: auto;
}

embed {
  vertical-align: top;
}

article, aside, audio, canvas, command, datalist, details, embed, figcaption, figure, footer, header, hgroup, keygen, meter, nav, output, progress, section, source, video {
  display: block;
}

mark, rp, rt, ruby, summary, time {
  display: inline;
}

textarea:focus, input[type=text]:focus, input[type=password]:focus {
  outline: 0 none;
}

.clearfix {
  display: block;
}

.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

html[xmlns] .clearfix {
  display: block;
}

* html .clearfix {
  height: 1%;
}

::selection {
  background: #ee9644;
  /* Safari */
  color: #fff;
}

::-moz-selection {
  background: #ee9644;
  /* Firefox */
  color: #fff;
}

input {
  border-radius: 0;
}

input:focus {
  outline: 0;
}

.input {
  border: 1px solid #ccc;
  padding: 2px;
  border-radius: 0;
}

.input:focus {
  outline: 0;
}

.page-break, .print-only {
  display: none;
}

.point {
  cursor: pointer;
}

.f_left {
  float: left !important;
}

.f_right {
  float: right !important;
}

.aligncenter {
  text-align: center;
}

.alignleft {
  text-align: left;
}

.alignright {
  text-align: right;
}

.relative {
  position: relative;
}

/*--- placeholder --*/
::-webkit-input-placeholder {
  opacity: 1 !important;
  color: #999999;
}

input::-moz-placeholder, textarea::-moz-placeholder {
  opacity: 1 !important;
  color: #999999 !important;
}

::-ms-input-placeholder {
  opacity: 1 !important;
  color: #999999;
}

input[type="text"], input[type="email"], input[type="password"], button, input[type="button"], input[type="submit"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-border-radius: 0px;
  border-radius: 0px;
}

button::-moz-focus-inner {
  border: 0 !important;
  outline: none !important;
}

:focus {
  outline: none !important;
}

::-moz-focus-inner {
  border: 0 !important;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: none;
}

label.error {
  color: red;
  display: block;
  font-size: 13px;
  font-weight: normal;
  text-align: left !important;
}

