/*custom checkbox
===========================*/
input.custom-checkbox {
  position: absolute;
  z-index: 1;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  opacity: 0;
  width: 58px;
  height: 18px;
  display: inline-block;
  margin: 0;
  cursor: pointer;
}

input.custom-checkbox + label {
  margin-right: 20px;
  padding-left: 22px;
  position: relative;
  z-index: 0;
  background: url(../../img/checkbox-icon-light.png) 0 2px no-repeat;
  display: inline-block;
  font-weight: normal;
  font-family: 'Roboto', sans-serif;
  height: 20px;
}

input.custom-checkbox:checked + label {
  background: url(../../img/checkbox-icon-light-checked.png) 0 2px no-repeat;
}

