			custom file input
==========================================*/
.file-input {
  padding-right: 40px;
  display: inline-block;
  margin-bottom: 15px;
  position: relative;
}

.file-input input[type="file"] {
  min-width: 100px;
  max-width: 100%;
  position: absolute;
  top: 0;
  opacity: 0;
  height: 27px;
  cursor: pointer;
}

.file-input input[type="file"]:hover + .custom-label {
  background: #679a4c;
}

.file-input span.custom-label {
  color: #fff;
  background: #7ab55c;
  padding: 0 10px;
  border: solid 1px #79b35b;
  border-radius: 3px;
  min-width: 100px;
  max-width: 200px;
  display: inline-block;
  text-align: center;
  font-size: 15px;
  height: 30px;
  line-height: 30px;
  word-break: break-all;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.file-input a.remove-img {
  position: absolute;
  right: 0;
  top: 5px;
  z-index: 999;
}

