  margin-left: .5em;
}

.tree ul:before {
  background: #fff;
  color: #fff;
  content: "";
  display: block;
  width: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  border-left: 1px solid;
}

.tree li {
  margin: 0;
  padding: 0 5px;
  line-height: 2em;
  color: #369;
  font-weight: 700;
  position: relative;
}

.tree ul li:before {
  /*content:"";*/
